	background-color: rgba(153, 27, 27, var(--tw-bg-opacity));
}

.bg-comix-banner {
  background: url(/static/media/Avengers.23ae6add.png) 10% 50% no-repeat, url("/static/media/Avengers logo.907cc903.png") 90% 50% no-repeat, rgb(17, 24, 39);
}

.text-color-main {
	--tw-text-opacity: 1;
	color: rgba(153, 27, 27, var(--tw-text-opacity));
}

.btn-main {
	display: flex;
	height: 38px;
	width: 101px;
	align-items: center;
	justify-content: center;
	background-image: url(data:image/png;base64,);
	background-size: cover;
	background-position: center;
	font-size: 0.875rem;
	line-height: 1.25rem;
	font-weight: 400;
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.btn-gray {
	display: flex;
	height: 38px;
	width: 101px;
	align-items: center;
	justify-content: center;
	background-image: url(data:image/png;base64,);
	background-size: cover;
	background-position: center;
	font-size: 0.875rem;
	line-height: 1.25rem;
	font-weight: 400;
	--tw-text-opacity: 1;
	color: rgba(255, 255, 255, var(--tw-text-opacity));
}

.line-clamp {
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  line-clamp: 4;
  -webkit-box-orient: vertical;
}

.single-clamp {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}



